@import url('https://fonts.googleapis.com/css2?family=Tenor+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playwrite+GB+S:ital,wght@0,100..400;1,100..400&family=Tenor+Sans&display=swap');

@font-face {
  font-family: 'Atteron';
  src: url('./fonts/Atteron.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
:root {
  --font-family: 'Atteron';
}

.App {
  text-align: center;
}

.header {
  margin: 0;
  display: flex;
}

/* left part of main page */
.main-left {
  text-align: left;
}

/* main-left-top part of main page */
.main-left-top {
}
/* main-left-top main-image */
.main-page-img {
  width: 27vw;
}
/* main-left-bottom part of main page */
.main-left-bottom {
  width: 40vw;
  text-align: left;
  margin-left: 7.5vw;
  margin-top: 5vh;
}
/* main-text in main-left-bottom part of main page */
.main-text {
  display: table;
  background-color: rgba(211, 211, 211, 0.24);
  flex: 0 0 auto; /* Maintain original size */
  margin-bottom: 10px; /* Adjust margin to create space between lines */
  font-family: "Playwrite GB S", cursive;
}
/* main-btn in main-left-bottom part of main page */
.main-page-btns {
  font-size: 14px;
  padding: 0 2vw;
  font-family: "Playwrite GB S", cursive;
  background-color: black;
  color: white;
}
.right-side-btn {
  font-size: 14px;
  padding: 1.5vh 3.5vw;
  font-family: "Playwrite GB S", cursive;
  background-color: rgb(211, 211, 211);
  color: rgb(38, 38, 38);
}
/* right part of main page */
.main-right {
     padding: 9vh 5vw 0 5vw;
}
/* main-right-top part of main page */
.main-right-top {
  
}
/* main-right-bottom part of main page */
.main-right-bottom {
  display: flex;
  flex-direction: column;
}
.social-icons {
  text-align: right;
}
.icons {
  color: black;
  margin: 20vh 20px;
}


/* portfolio page */
.portfolio-body {
  display: flex;
  margin: 0 10vw;
  justify-content: space-between;
}
.projects{
  text-align: center;
  width: 23vw;
}
.projects  .main-text{
  background-color: white;
  text-align: center;
  width: 23vw;
}
/* email and portfoilio components */
.secondary-header {
  display: flex;
  justify-content:space-between;
}
/* for the contact form */
.contact-form {
  display: flex;
  width: 35vw;
  flex-direction: column;
  margin: auto;
}
.email-form {
  margin: 2vh 0;
}
/* email page */